*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

:root{
  --blu1: rgb(1, 20, 35);
  --blu-mezzo: rgb(3, 32, 52);
  --blu2: rgb(9, 52, 86);

  --giallo-chiaro1: rgb(252, 226, 78);
  --giallo-chiaro2: rgb(255, 221, 29);
}

html, body, #root{
  height: 100%;
}

.app{
  min-height: 100%;
  display: flex;
  flex-direction: column;

  background: var(--blu1);
}

.container-comp{
  display: flex;
}

input{
  margin: 1em 0 1em 0.5em;
  padding: 0.1em 0 0.1em 0.2em ;

  color: var(--blu1);
  border-radius: 0.3em;
  border-style: solid;

  font-size: 0.88em;
  font-weight: bold;

}


select{
  margin: 1em 0 1em 0.5em;
  padding: 0.1em 0 0.1em 0.2em ;

  color: var(--blu1);
  border-radius: 0.3em;
  border-style: solid;

  font-size: 0.88em;
  font-weight: bold;
  
}

/* FormComp style */

.form-comp{
  padding-left: 1em;
  width: 35%;

  color: var(--giallo-chiaro1);

  letter-spacing: 0.03em;
  
  display: flex;
  flex-direction: column;
}

.form-comp h1{

  margin-top: 0.6em;
  margin-bottom: 0.6em;

  font-size: 2.15em;
  letter-spacing: 0.04em;
}

.form-comp textarea{
  margin-top: 1.4em;
  padding: 0.1em;
  
  width: 70%;

  color: var(--blu1);
  border-radius: 0.3em;
  border-style: solid;
  
  font-size: 1.05em;
  font-weight: bold;
}

.text-area-need{
  margin-top: 0.8em;
  margin-bottom: 0.5em;
  width: 70%;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.text-area-need p{
  margin-top: 0.2em;

  color: rgb(230, 0, 0);

  text-align: center;
  font-size: 1.1em;
  font-weight: bold;
  letter-spacing: 0.09em;
  
}

.form-comp input[name='anno']{
  margin-top: 2em;
  width: 4em;
}

.form-comp input[name='giorno']{
  width: 3em;
}

.form-comp input[name='orario']{
  margin-bottom: 1.2em;
}

.form-comp label{
  font-size: 1.05em;
}

.orario-div{
  display: flex;
  align-items: center;
}

.orario-need{
  margin-left: 0.8em;

  display: flex;
  align-items: center;
}

.orario-need p{

  margin-left: 0.4em;


  color: rgb(230, 0, 0);

  text-align: center;
  font-size: 1.1em;
  font-weight: bold;
  letter-spacing: 0.09em;
}

.form-comp button{

  margin-top: 1.7em;
  margin-bottom: 1.7em;
  padding: 0.6em 0.82em 0.65em 0.82em;
  
  background: var(--giallo-chiaro2);
  border-radius: 0.5em;
  border: 0.2em solid var(--giallo-chiaro2);

  font-size: 0.85em;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.03em;

  cursor: pointer;
}

.form-comp button:hover{
  color: var(--giallo-chiaro2);
  background: var(--blu1);
  border: 0.2em solid var(--giallo-chiaro2);
}

.notifica{
  padding-top: 0.8em;
  padding-bottom: 0.8em;

  color: var(--giallo-chiaro2);

  font-family: Geneva, Verdana, sans-serif;
  font-size: 0.87em;
  font-weight: bold;
  letter-spacing: 0.04em;

  opacity: 0;

  pointer-events: none;
}

/* anima il messaggio di notifica per farlo scomparire (opacity) (handled with js) */
@keyframes visible-to-not-visible{
  0%{
    opacity: 1;
  }
  40%{
    opacity: 0.8;
  }
  100%{
    opacity: 0;
  }
}

/* fine FormComp style */


/* PromsComp style */

.proms-comp{
  margin: 1em;
  padding-left: 0.7em;
  padding-bottom: 1em;
  width: 35%;

  color: var(--giallo-chiaro1);
  border: 0.1em solid var(--giallo-chiaro1);
  border-radius: 0.4em;

  letter-spacing: 0.03em;
  
  display: flex;
  flex-direction: column;
}

.days-comp-width{
  width: 65%;
}

.proms-comp h1{
  font-size: 1.8em;
  letter-spacing: 0.02em;
}

.top-div{
  margin-top: 0.5em;
  display: flex;
  justify-content: space-between;
}

.close-div{
  margin-top: 0.45em;
  margin-right: 0.6em;

  border: 0.15em solid var(--giallo-chiaro1);
  border-radius: 50%;

  font-size: 0.95em;
  transform: scale(0.85);

  cursor: pointer;
}

.top-div img{
  padding: 0.35em 0.25em 0 0.65em;

  transform: rotate(45deg);
}

.prom-div-comp{
  margin: 0.9em 0.9em 0.1em 0.1em;
  padding: 0.5em 0.6em 0.7em 0.7em;

  background: var(--blu-mezzo);
  border-radius: 0.4em;
}

.small-closing-div{
  position: relative;
  top: -0.25em;
  right: -0.35em;
  margin-left: 0.5em;
  float: right;

  border: 0.15em solid var(--giallo-chiaro1);
  border-radius: 50%;

  transform: scale(0.75);

  cursor: pointer;
}

.small-closing-div img{
  padding: 0.2em 0.15em 0 0.35em;

  transform: rotate(45deg);
}

.prom-div-comp .testo-p{
  padding-top: 0.2em;

  word-wrap: break-word;
}

.prom-div-comp .data-p{
  padding-top: 0.9em;
  word-wrap: break-word;

  font-size: 0.95em;
  font-weight: bold;
  letter-spacing: 0.04em;
}

.separatore{
  margin: 2em 0.9em 1em 0.1em;
  
  height: 2em;

  border-radius: 0.2em;
}

.proms-odierni{
  padding-bottom: 2em;
}

.vuoto{
  padding: 1em 0.6em 0.7em 0.7em;
}

/* fine PromsComp style */


/* CalendarComp style */

.calendar-comp{
  width: 65%;

  color: var(--giallo-chiaro1);

  text-align: center;
}

.calendar-comp h1{
  margin-top: 0.6em;
  margin-bottom: 1.3em;

  font-size: 2.15em;
  letter-spacing: 0.04em;
}

.calendar-comp input{
  margin-right: 2em;
  width: 4em;
}

.calendar-comp span{
  margin-bottom: 2.5em;

  display: inline-block;
}

.div-calendario{
  margin-left: 0.9em;
  margin-right: 0.9em;

  padding: 1em 0.5em 0 0.5em;

  background: var(--blu-mezzo);
  border-radius: 0.6em;
  
  display: grid;
  grid-template-columns: repeat(7, 1fr);
}

.div-calendario p{

  margin-bottom: 1.8em;
  border-radius: 0.3em;

  font-weight: bold;
  letter-spacing: 0.1em;
  text-align: center;
}

.calendar-day-comp h2{
  margin: 0 0.19em 1em 0.19em;

  padding-top: 0.3em;
  padding-bottom: 0.3em;
  
  color: white;
  background: var(--blu2);
  border-radius: 0.3em;

  text-align: center;

  cursor: pointer;
}

.calendar-day-comp h2:hover{
  background: rgb(1, 73, 128);
}

.evidenziato{
  border: 0.05em solid var(--giallo-chiaro1);
  border-bottom: 0.1em solid var(--giallo-chiaro1);
}

/* fine CalendarComp style */

/* FooterComp style */

.footer-comp{
  margin-top: auto;

  width: 100%;
  height: 3.5em;

  color: white;
  background: rgb(20, 24, 21);
  border-top: 0.2em solid black;
}

.footer-comp p{
  padding-top: 1em;

  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: bold;
  font-size: 0.9em;
  text-align: center;
  letter-spacing: 0.03em;
}

/* fine FooterComp style */

@media screen and (max-width: 1024px){
  .container-comp{
    display: block;
  }

  .form-comp{
    width: 100%;
  }

  .proms-comp{
    margin-left: 0;
    width: 100%;
    transform: scale(0.9);
  }

  .calendar-comp{
    margin-top: 3em;
    padding-bottom: 2em;

    width: 100%;

    border-top: 0.4em solid black;
  }

}